* {
  margin: 0;
  padding: 0;
  font-family: "Lucida Console";
}

.App {
  background: #282631;
}

body {
  background-color: #282631;
}

header {
  width: 100%;
  text-align: center;
  background-color: whitesmoke;
  justify-content: center;
}

.scoreboard {
  color: whitesmoke;
  font-size: 46px;
  margin: 50px auto;
  position: relative;
  border: 3px solid whitesmoke;
  border-radius: 4px;
  text-align: center;
  width: 300px;
  padding: 15px 20px;
}

.name-banner {
  background-color: #e2584d;
  color: whitesmoke;
  font-size: 14px;
  padding: 2px 10px;
}

#user-banner {
  position: absolute;
  top: 30px;
  left: -25px;
}
#computer-banner {
  position: absolute;
  top: 30px;
  right: -40px;
}

header h1 {
  color: #282631;
  font-size: 60px;
}

#comment {
  color: whitesmoke;
  font-size: 46px;
  margin-top: 50px;
  text-align: center;
}

.buttons {
  display: flex;
  justify-content: center;
  align-items: center;
}

img {
  margin: 10px 35px;
  width: 200px;
  border-radius: 50%;
  padding: 0px;
}

footer {
  position: fixed;
  text-align: center;
  color: #282631;
  bottom: 0;
  width: 100%;
  height: 60px;
  background: whitesmoke;
  margin-bottom: 0px;
}

a,
a:hover,
a:focus,
a:active,
a:visited {
  text-decoration: none;
}

button {
  background-color: transparent;
  border: 7px solid whitesmoke;
  border-radius: 50%;
  width: 300px;
  height: 300px;
  margin: 20px;
  padding: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
}

footer h4 {
  margin: 20px auto;
}

.red-glow {
  border: 4px solid #fc121b;
  box-shadow: 0 0 10px #d01115;
}

.green-glow{
  border: 4px solid #4dcc7d;
  box-shadow: 0 0 10px #31b43a;
}

.gray-glow{
  border: 4px solid #464647;
  box-shadow: 0 0 10px #25292b;
}
